import Vue from "vue";
import Router from 'vue-router';

//加路由组件
import Main from '../views/Main'

import KeHu from "../views/KeHu";
import ZhunKe from "../views/ZhunKe";
import BenDi from "../views/BenDi";
import Update  from "../views/Update";
import KeZi from "../views/KeZi";
import ZhunZi from "../views/ZhunZi";
import XinZeng from "../views/XinZeng";

Vue.use(Router)
//导出一个接口
export default new Router({
  //不带#号
  mode: 'history',
  //routes是一个数组
  routes: [
    //配路由
    {
      path: '/',
      props: true,
      component: Main,
      name: 'Main'
      //嵌套路由
    },
    {
      path: '/main',
      props: true,
      component: Main,
      name: 'Main'
      //嵌套路由
    },{
    path: '/kehu',
    props: true,
    component: KeHu,
      name: 'KeHu'

    },{
    path: '/kezi',
      component: KeZi,
      name: 'Kezi'
    },{
    path: '/zhunke',
      component:ZhunKe,
      name: 'ZhunKe'
    },{
    path: '/zhunzi',
      component: ZhunZi,
      name: 'ZhunZi'
    },{
    path: '/xinzeng',
      component: XinZeng,
      name: 'XinZeng'
    },{
    path: '/bendi',
      component: BenDi,
      name: 'BenDi'
    },{
      path: '/update',
      component: Update,
      name: 'Update'
    }
  ]
});



